:root {
    --white: #fdfffc;
    --black: #020100;
}

:root.normal {
    --font: "Merom Sans";
    --marker-font: "Free Metto";
    --white: #ffecd1;
    --black: #001524;
    --screen: #15616d;
    --error-screen: #78290f;
