    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    background-color: var(--screen);
    border: 3px solid var(--border-colour);
}

.scorecard > [class*="-scorecard"] {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.information-container { font-size: 1.5rem; }

.info-message { font-size: 1rem; }


